<template>
  <div class="wrapper">
    <div v-for="good in goods" :key="good.id" @click="clickGoods(good.id)">
      <!--商品详情-->
      <div style="width: 95%;margin: auto;" class="aui-margin-b-15 white-bg">
        <!--商品图片-->
        <div>
          <img :src="good.img + '?imageView2/1/w/600/interlace/1'"/>
        </div>
        <!--价格部分-->
        <div style="background: #dd6161;height: 56px;">
          <div class="aui-row aui-text-white aui-padded-t-15">
            <div class="aui-col-xs-6">
              <span class="font-size-16 aui-margin-l-15">￥{{good.price}}</span><span class="aui-font-size-14">.00</span>
              <span class="aui-margin-l-15 aui-font-size-14"
                    style="text-decoration:line-through;">￥{{good.show_price}}</span>
            </div>
            <div class="aui-col-xs-6 aui-text-right">
              <span class="aui-margin-r-15 font-size-14">已售{{good.sale_num}}份</span>
            </div>
          </div>
        </div>
        <!--商品名称-->
        <div class="aui-margin-15">
          <div class="aui-font-size-18 text-twoline" style="line-height: 24px;">
            {{good.name}}
          </div>
        </div>
        <!--小编推荐部分-->
        <div class="aui-margin-15">
          <div class="aui-row">
            <div class="aui-col-xs-2">
              <img :src="good.editor.avatar" class="aui-margin-t-10"
                   style="width: 30px;height: 30px;border-radius: 50%;margin: auto;"/>
            </div>
            <div class="aui-col-xs-10">
              <div class="font-size-14 text-twoline" style="line-height: 28px;">
                {{good.remark}}
              </div>
            </div>
          </div>
        </div>
        <div class="aui-text-center">
          <div class="aui-btn aui-btn-primary">点击查看详情</div>
        </div>
        <!--占位符-->
        <div style="height: 30px;"></div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">

  import {jumpToGoods} from '../../assets/js/common'

  export default {
    name: 'goods_list',
    data() {
      return {}
    },
    props: {
      goods: {      //传入商品列表
        type: Array,
        require: true
      }
    },
    methods: {
      //点击商品
      clickGoods: function (goods_id) {
        jumpToGoods({goods_id: goods_id, router: this.$router});
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



